import React, { useState } from "react"
import { Grommet, Box, Button, Text } from 'grommet';
import { grommet } from 'grommet/themes';
import '../pages/index.css'

const Home = ({ children }) => {
  const [sidebar, setSidebar] = useState(false);
  return <Grommet full theme={grommet}>
      <Box
        gridArea="header"
        direction="row"
        align="center"
        pad={{ horizontal: 'medium', vertical: 'small' }}
        background="brand"
      >
        <Button margin={{ right: '20px'}} onClick={() => setSidebar(sidebar)}>
          <Text size="large">OpenFengShui</Text>
        </Button>
      </Box>
      <Box 
        align="center"
        justify="center"
      >
        <Box 
          width={{
          min: '360px',
          max: '760px'
        }} >
          { children }
        </Box>
      </Box>
    </Grommet>
}

export default Home
