import React, { useState, useEffect } from "react"
import { Layer, List, Select, Box, Button, Heading } from 'grommet';
import styled from 'styled-components'
import Square from '../components/Square'
import QiMen from '../components/Square/QiMen'
import { gregorianToBazi } from '../cm-lib/FourPillar'
import DateTimePicker from '../components/DateTimePicker'
import Ju from '../cm-lib/reference/Ju.json'
import ChartList from '../cm-lib/reference/DetailChart.json'
import { formation } from '../cm-lib/reference/Formation'
import { DEAULT_DATE_FORMAT, BRANCH, DATETIME_FORMAT, DIRECTION } from '../cm-lib/constants'
import { format, parseISO, addDays, differenceInCalendarDays } from 'date-fns';
import Bazi from './bazi'

const BoxWrapper = styled(Box)`
  width: 32%;
  margin: 2px;
`

const search = (chart, palace) => {
  const now = new Date();
  const searchEndDate = addDays(now, 5);
  const diff = differenceInCalendarDays(searchEndDate, now);
  return [...Array(diff).keys()].map(d => {
      const compareDate = addDays(now, d);
      const key = format(compareDate, DEAULT_DATE_FORMAT);
     
      return ChartList && ChartList[key]
      .reduce((a, e) => {
          const formationIndex = chart.findIndex(o => e.structure === o.structure && e.hour === o.hour)
          if(formationIndex > -1 && palace.includes(DIRECTION[chart[formationIndex].e.i])) {
              return a.concat([{
                  ...e,
                  f: { ...chart[formationIndex].e, d: DIRECTION[formationIndex] }
              }])
          }
          return a;
      }
      , [])
      .map(ee =>  {
          const hour = BRANCH.indexOf(ee.hour.substr(1, 1)) * 2;
          return { ...ee, date: `${key}T${hour.toString().padStart(2, '0')}:00:00`};
      })
  })
  .reduce((a, n) => a.concat(n), [])
}

const handleSearch = (searchFormation, palace) => {
    
  const found = Ju.filter(v => v.chart.find(g => g.f && g.f.indexOf(searchFormation) > -1 ))
  .map(e => ({ structure: e.structure, hour: e.hour, e: { ...e.chart.find(e => e.f === searchFormation), i: e.chart.findIndex(e => e.f === searchFormation) }}))
  
  const result = search(found, palace);
  const display = (v, square) => `${format(parseISO(`${v}`), DATETIME_FORMAT)} ${square.heaven} ${square.star} ${square.door} ${square.god} ${square.earth} ${DIRECTION[square.i]}`
  return result.map((v, i) => ({ ...v, display: display(v.date, v.f)}))
}

const Home = () => {
  const [dateObject, setDateObject] = useState(new Date());
  const [chart, setChart] = useState({});
  const [useFormation, setUseFormation] = useState(Object.keys(formation)[0]);
  const [showList, setShowList] = useState(false)
  const [searchList, setSearchList] = useState([])
  const [, setBazi] = useState('')

  useEffect(() => {
    if(dateObject) {
      const year = dateObject.getFullYear();
      const month = dateObject.getMonth()
      const date = dateObject.getDate();
      const hour = dateObject.getHours();
      setBazi(gregorianToBazi(year, month + 1, date, hour))
      const chartDate = format(dateObject, 'yyyyMMdd')
      const ch = ChartList[chartDate][Math.floor((hour) / 2)];
      
      const foundChart = Ju.find(e => e.structure === ch.structure && e.hour === ch.hour);

      setChart(foundChart)
    } else {
      
    }

  }, [dateObject])
  return <>
    <Heading level={2}>Qi Men Dun Jia Hour Chart</Heading>
    <Box>
        <DateTimePicker selectDate={d => setDateObject(d)} />
    </Box>

    
    <Box direction="row">
      <Box>
        <Select
          options={Object.keys(formation)}
          value={useFormation}
          onChange={({ option }) => setUseFormation(option) }
        />
      </Box>
      
      <Box>
        <Button primary label="Search" onClick={() => {
          setSearchList(handleSearch(useFormation, DIRECTION))
          
          
          setShowList(true)
        }
          
        }/>
      </Box>
    </Box>
    <br />
  <Bazi />
  <br />
  <Box 
    background="brand"
    alignSelf="center"
    justify="center"
    direction="row"
    wrap={true}
    width={{
      min: '460px',
      max: '760px'
    }}
  >

  {
    chart.chart && chart.chart.map((e, i) => <BoxWrapper
      border={true}
    >
    <Square data={{
        structure: chart.structure,
        ...e,
        index: i
      }} Template={QiMen} />
    </BoxWrapper>)
  }
  </Box>
    {
      showList && <Layer 
        display={showList}
        onClickOutside={() => setShowList(false)}
      >
        <List
          primaryKey="display"
          secondaryKey="percent"
          data={searchList}
        />
        <Button label="close" onClick={() => setShowList(false)} />
      </Layer>
    }
  </>
}

export default Home
