import React from 'react';
import { Text } from 'grommet'
import styled from 'styled-components'

const Symbol = styled.div`
    position absolute;
    ${props => [0, 3, 6].includes(props.index) ? 'left: 10px;' : '' }
    ${props => [2, 5, 8].includes(props.index) ? 'right: 10px;' : '' }
    ${props => [6, 7, 8].includes(props.index) ? 'bottom: 10px;' : '' }
    ${props => [1, 4, 7].includes(props.index) ? 'left: 50%;' : '' }
    ${props => [1, 4, 7].includes(props.index) ? 'margin-left: -10px;' : '' }
    width: 20px;
    text-align: center;
`

const index = ({ index, ...props }) => {
    const cardinal = ["SE", "S", "SW", "E", "", "W", "NE", "N", "NW"]
    return <>
        <Symbol index={0}>
            <Text color="white" size="xlarge">{ props.heaven }</Text>
        </Symbol>
        <Symbol index={1}>
            <Text color="white" size="xlarge">{ props.star }</Text>
        </Symbol>
        <Symbol index={2}>
            <Text color="white" size="xlarge">{ props.god }</Text>
        </Symbol>
        {
            index === 4 && <Symbol index={4}>
            <Text color="white" size="xlarge">{ props.structure }</Text>
            </Symbol>
        }

        <Symbol index={6}>
            <Text color="white" size="xlarge">{ props.earth }</Text>
        </Symbol>
        <Symbol index={7}>
            <Text color="white" size="xlarge">{ props.door }</Text>
        </Symbol>
        <Symbol index={8}>
            <Text color="white" size="medium">{ cardinal[index] }</Text>
        </Symbol>
        </>
    ;
}

export default index;