import React, { useState, useEffect } from "react"
import { Box } from 'grommet';
import styled from 'styled-components'
import Square from '../components/Square'
import Bazi from '../components/Square/Bazi'
import { gregorianToBazi } from '../cm-lib/FourPillar'


const BoxWrapper = styled(Box)`
  width: 25%;
`

const Home = () => {
  const [dateObject] = useState(new Date());
  const [chart, setChart] = useState([]);

  const [, setBazi] = useState('')

  useEffect(() => {
    if(dateObject) {
      const year = dateObject.getFullYear();
      const month = dateObject.getMonth()
      const date = dateObject.getDate();
      const hour = dateObject.getHours();
      const baziChart = gregorianToBazi(year, month + 1, date, hour)
      setBazi(baziChart)
      setChart([
        { symbol: baziChart.GanZhiYear.ganzhi.substr(0, 1) },
        { symbol: baziChart.GanZhiMonth.ganzhi.substr(0, 1) },
        { symbol: baziChart.GanZhiDay.ganzhi.substr(0, 1) },
        { symbol: baziChart.GanZhiHour.ganzhi.substr(0, 1) },
        { symbol: baziChart.GanZhiYear.ganzhi.substr(1, 1) },
        { symbol: baziChart.GanZhiMonth.ganzhi.substr(1, 1) },
        { symbol: baziChart.GanZhiDay.ganzhi.substr(1, 1) },
        { symbol: baziChart.GanZhiHour.ganzhi.substr(1, 1) },
      ])
    } else {
      
    }

  }, [dateObject])
  return <>
  <Box 
    background="neutral-3"
    alignSelf="center"
    justify="center"
    direction="row"
    wrap={true}
    width={{
      min: '460px',
      max: '760px'
    }}
  >
  {
    chart && chart.map((e, i) => <BoxWrapper
      border={true}
    >
    <Square data={{
        structure: chart.structure,
        ...e,
        index: i
      }} Template={Bazi} />
    </BoxWrapper>)
  }
  </Box>

  </>
}

export default Home
